
/* style for the home main container */

.home-container
{
    height: 300px;
    color: beige;
    margin-left: 100px;
    
    
}

/* style for the text , h1  */

.home-content h1
{
    padding: 0px;
    padding-top: 100px;
    margin-left: 40px;
    color: black;
    font-size: 60px;
    font-family: 'Oswald', sans-serif;

}

/* fade in animation for text h1 separate */

.fade-in-text {
    opacity: 0; 
    animation: fadeIn 3s ease-in forwards; 
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
/* styling h2 tag  */

.home-content h2
{
   color: rgb(140, 12, 125);  
   margin-left:30px;
   padding: 10px;
}

/* it is the style  for the button */

.home-button
{
    
    padding: 0px;
    margin-left: 50px;
    margin-top: 18px;
    display: flex; 
    align-items: center; 
    gap: 15px; 
}

.home-button .btn-hire
{
    background-color: rgb(22, 176, 215);
    color: white;
    width: 140px;
}
.home-button .btn-social
{
    background-color: rgb(22, 176, 215);
    color: white;
    width: 140px;
    margin-left: 30px;
}
.home-button .btn-hire:hover
{
  background-color: rgb(22, 176, 215);
    color: black;
    width: 160px;
    transition: 1s;
}



.social-icons {
  display: flex; 
  gap: 5px; 
}
.social-icon {
  color: black; 
  font-size: 24px; 
  text-decoration: none; 
  transition: color 0.3s ease; 
}








/* =====MOBILE DEVICE==== */

@media (max-width: 600px) {
  .home-container {
    height: auto;
    color: beige;
    padding: 10px;
    margin-top: 100px;
    margin-left: 0px; 
  }

  .home-content h1 {
    padding-top: 20px;
    margin-left: 10px; 
    font-size: 40px;
    text-align: left; 
  }

  .home-content h2 {
    color: rgb(140, 12, 125);  
    margin-left: 10px; 
    padding: 5px;
    font-size: 18px;
    text-align: left; 
  }

  .home-button {
    padding: 0;
    margin: 20px 10px; 
    display: flex;
    flex-direction: column; 
    align-items: flex-start; 
    gap: 10px; 
  }

  .home-button .btn-hire,
  .home-button .btn-social {
    background-color: rgb(22, 176, 215);
    color: white;
    width: 120px;
    font-size: 14px;
    text-align: left; 
  }

  .home-button .btn-hire:hover {
    background-color: rgb(22, 176, 215);
    color: black;
    width: 140px;
    transition: 1s;
  }
  .social-icons {
    gap: 10px;
    justify-content: flex-start; 
  }

  .social-icon {
    font-size: 20px;
    text-align: left; 
  }
}
