.heading1
{
  margin-top: 100px;
  font-family: 'Oswald', sans-serif;
  margin-left: 120px;
}


.contact-container {
  display: flex;
  justify-content: space-between;
  padding: 50px;
  max-width: 1200px;
  margin: auto;
 
  margin-left: 120px;
}


.contact-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  
}

.contact-item {
  display: flex;
  align-items: center;
  background-color: #f3f4f6;
  padding: 10px;
  border-radius: 10px;
}

.contact-icon {
  font-size: 2rem;
  color: #3b82f6;
  background-color: #f3f4f6;
  margin-right: 20px;
  border-radius: 10px;
}

.contact-text {
  font-size: 1.1rem;
  color: #333;
  
}

.contact-form {
  flex: 1;
  display: flex;
  justify-content: center;
}

form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 400px;
}

input, textarea {
  padding: 15px;
  font-size: 1rem;
 
  border: 1px solid #ccc;
  background-color: #f3f4f6;
}


button {
  padding: 15px;
  font-size: 1rem;
  width: 180px;
  background-color: #3b82f6;
  color: white;
  border: none;
  
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2563eb;
}

/* Responsive */
@media (max-width: 768px) {
  .contact-container {
    flex-direction: column;
    align-items: center;
  }

  .contact-form {
    width: 100%;
    max-width: 500px;
  }

  .contact-info {
    width: 100%;
    max-width: 500px;
    margin-bottom: 30px;
  }
}



/* Mobile specific styles */
@media (max-width: 600px) {
  .heading1 {
    margin-top: 50px; /* Reduce top margin for mobile */
    margin-left: 10px; /* Center content with reduced left margin */
    font-size: 24px; /* Adjust font size for better readability */
    text-align: center; /* Center the heading text */
  }

  .contact-container {
    flex-direction: column; /* Stack items vertically */
    padding: 20px; /* Reduce padding for mobile */
    margin-left: 0; /* Remove left margin */
    margin: 0; /* Reset margins for container */
  }

  .contact-info {
    width: 100%; /* Full width for mobile */
    margin-bottom: 20px; /* Space between contact info and form */
  }

  .contact-item {
    padding: 10px; /* Keep padding for items */
    flex-direction: row; /* Ensure items are in a row */
    align-items: center; /* Center vertically */
  }

  .contact-icon {
    font-size: 1.5rem; /* Smaller icon size for mobile */
    margin-right: 10px; /* Reduced margin for smaller screens */
  }

  .contact-text {
    font-size: 0.9rem; /* Smaller text size for better mobile readability */
  }

  .contact-form {
    width: 100%; /* Full width for mobile */
    max-width: none; /* Remove max-width restriction */
  }

  input, textarea {
    padding: 10px; /* Reduced padding for inputs */
    font-size: 0.9rem; /* Smaller font size */
  }

  button {
    width: 100%; /* Full width button */
    max-width: 180px; /* Limit button width */
    margin: auto; /* Center button */
  }
}
