.heading-box {
    cursor: pointer; 
    background-color: #DAA520; 
    padding: 10px; 
    text-align: left; 
    transition: background-color 0.3s ease; 
    border: 2px solid #d3d3d3; 
    margin-left: 120px;
    margin-top: 100px;
    color: #000;
    font-family: 'Oswald', sans-serif;
    border-radius: 10px;
  }
  .heading-box:hover {
    
    background-color: #F1C40F; 
    color: black; 
  }
  .heading-box.clicked {
    background-color: #6A7D8A;  
    color: white; 
  }
  
  .ptag {
    text-align: center;
    font-size: 20px;
    margin: 20px 0; 
  }
  
  .row1 {
    display: flex;
    justify-content: flex-start; 
    flex-wrap: wrap; 
    
    margin-left: 120px;
    width: 100%;
  }
  
  .card1 {
    position: relative;
    width: 100%; 
    max-width: 300px; 
    margin: 15px;
    border: 2px solid #d3d3d3; 
    border-radius: 8px; 
    transition: all 0.3s ease; 
  }
  
  .card1:hover {
    background-color: #f0f0f5; 
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px; 
  }
  
  .card-image img {
    height: 200px; 
    width: 100%; 
    object-fit: cover; 
  }
  
  .centered-text {
    position: absolute;
    bottom: 10px; 
    left: 50%; 
    transform: translateX(-50%);
    z-index: 1; 
    color: white; 
    font-size: 16px; 
  
}
.centered-text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /* Centers the text */
  z-index: 1;
  color: white;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  padding: 10px 20px;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent background for readability */
  border-radius: 8px;
}


  


/* ======MOBILE VIEW===== */

  /* Mobile specific styles */

@media (max-width: 600px) {
  .heading-box {
    margin-left: 10px; 
    margin-top: 50px; 
    padding: 10px; 
    font-size: 18px; 
    text-align: center; 
  }

  .row1 {
    display: flex;
    flex-direction: column; 
    align-items: center;
    justify-content: center; 
    margin-left: 0; 
    width: 100%; 
  }

  .card1 {
    width: 90%; 
    margin: 15px 0;
    max-width: none; 
  }

  .card-image img {
    height: auto; 
    width: 100%; 
  }

  .centered-text {
    font-size: 14px; 
    bottom: 5px; 
  }

  .ptag {
    font-size: 18px; 
    margin: 15px 0; 
  }
}
