/* Mobile navigation container */

.mobile-nav {
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
    background-color: #1e1e1e; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); 
}

/* Navigation header (title bar) */

.mobile-nav-header {
    height: 55px;
    width: 100%;
    background-color: #1e1e1e;
    color: white;
    padding: 8px;
    display: flex; 
    align-items: center; 
}

/* Navigation title styling */

.mobile-nav-title {
    margin-left: 20px;
    color: white;
    font-weight: bold;
    text-transform: capitalize;
    vertical-align: middle;
    font-size: 20px;
}

/* Paragraph styling in mobile nav */
.mobile-nav p {
    color: white;
    margin: 0; 
    padding: 10px 0;
    cursor: pointer; 
}

/* Hover effect for paragraphs */

p:hover {
    color: blue;
}

/* Mobile navigation menu dropdown or side menu */

.mobile-nav-menu {
    height: auto;
    width: 200px;
    background-color: #1e1e1e;
    padding: 10px;
    border-bottom-right-radius: 20px;
    position: fixed; 
    top: 55px; 
    left: 0;
    z-index: 9999; 
}

/* Add padding to the body to prevent content from hiding under the sticky nav */

body {
    padding-top: 55px; 
}



/* ========mobile view===== */

@media (min-width:600px)
{
    .mobile-nav{
        display: none;
    }
}