
/* styles for about section */

.about {
    margin: 160px auto;
    padding: 20px;
    display: flex;
    flex-direction: column; 
    align-items: left; 
    justify-content: center; 
    background-color: white;
    width: 80%; 
    border-radius: 10px;
    
}

/* styles for the text and paragraph */

.about-content {
    text-align: left;  
    width: 100%;  
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-content h1 {
    color: black;
    font-family: 'Oswald', sans-serif;
    text-align: left;
    font-size: 24px;  
    margin-bottom: 30px;
}

/* Managing the text content */

.about-content p {
    text-align: justify;
    font-family: 'Courier New', Courier, monospace;
    font-size: 16px; 
    margin: 0 20px;  
}







/* =====Mobiile device=== */

@media (max-width:600px)
{
    
    .about {
        margin: 20px auto;
        padding: 10px;
        width: 100%;  
        flex-direction: column; 
        align-items: center; 
        justify-content: center;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    }

    .about-image img {
        width: 100%; 
        height: auto;  
        margin: 0 0 20px 0;  
        display: flex;
        justify-content: center;
    }

    .about-content {
        width: 100%;  
        margin: 0;
        padding: 0 10px;
        display: flex;
        flex-direction: column;
        text-align: left;  
    }

    .about-content h1 {
        text-align: center;  
        margin-bottom: 15px;
    }

    .about-content p {
        text-align: justify;
        margin-bottom: 10px;
    }
    

  
}