/* Styling for active nav link (blue underline like an <hr>) */

.nav-link.active::after {
    content: '';
    display: block;
    width: 100%; 
    height: 3px; 
    background-color: blue; 
    position: absolute;
    bottom: 0; 
    left: 0;
    /* margin-top: 2px;  */
}

/* Hover effect for nav-link: blue text color */

/* .nav-link:hover {
    color: blue;
} */

/* Optional: hover effect for the hr-like line on hover */

.nav-link:hover::after {
    background-color: blue; 
}












/* Styling for the nav links */
.nav-link {
    cursor: pointer;
    margin-left: 5px;
    position: relative;
    /* padding-bottom: 2px; */
    display: inline-block;
    overflow: hidden;
}

/* Solid bar animation effect on hover */
.nav-link::before {
    content: '';
    /* display: block; */
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 3px;
    background-color: blue;
    transition: width 0.4s ease; /* Adjust the timing for smooth animation */
}

/* On hover, expand the solid bar to full width */
.nav-link:hover::before {
    width: 100%;
}

/* Optional: Change text color on hover */
.nav-link:hover {
    color: blue;
}




