/* style for project */



/* Styling for the project box */
.project-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    background-color: #DAA520; /* Matte black or any preferred color */
    color: black;
    padding: 10px 20px;
    border-radius: 8px;
    width: 90%;
    margin-left: 120px;
    margin-top: 50px;
    font-family: 'Poppins', sans-serif;
    transition: background-color 0.3s ease;
}

.project-box h1 {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

.toggle-icon {
    font-size: 24px;
    font-weight: bold;
    transition: transform 0.3s ease;
}

/* Change color and transform on hover */
.project-box:hover {
    background-color: #F1C40F; /* Slightly brighter black on hover */
}

.project-box:hover .toggle-icon {
    transform: scale(1.2); /* Slightly enlarge the icon on hover */
}

.project h1 
{
    text-align: left;
    
    font-family: 'Oswald', sans-serif;
    margin-top: 100px;
    width: 20%;
    margin-left: 120px;
    
    
}
.card:hover
{
    background-color: #fff;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transition: all 1s ease;

}

.second
{
    margin-top: 40px;
    margin-bottom: 20px;
}

#ads
{
    margin: 30px 0px 30px 100px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    margin-top: 40px;
   
}
.Colour
{
    background-color: #51d0de;
    
    position: absolute;
    left: -10px;
    top: -20px;
    text-align: center;
    border-radius: 20px 10px 20px 10px;
    color: white;
    padding: 5px 10px;
    font-size: 14px;
}

.card img
{
    height: 200px;
    width: 100%;
}


.card-image-overly
{
   margin: 2px 2px 1px 2px;
    margin-left: 25px;
}
.card-image-overly span
{
    display: inline-block;
    margin: 2px 5px 5px 2px;
    
}
.card-detail-badge
{
    
    background-color: #007bff;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    margin: 10px; 
   
   
    
    margin-left: 15px;
    margin-top: 30px;
    font-size: 18px;
   
    padding: 5px 10px;
    
   
}
.login 
{
    text-align: center;
    margin-top: 10px;
    color:#e62739;
    font-weight: bold;
}
.ad-btn 
{
     text-decoration: none; 
  color: inherit; 
  background-color: #091f36; 
  border-radius: 10px; 
  transition: background-color 0.3s; 
  padding: 5px 10px;
  text-transform: uppercase;
  width: 100px;
    height: 40px;
    font-size: 15px;
    line-height: 35px;
    margin: 20px auto 20px auto;
    text-align: center;
    overflow: hidden;
    position: relative;
    display: inline-block;
    
    
    

}
.ad-btn:hover
{
    
    background-color: #fff;
    color: #138781;
    background: transparent;
    border: 2px solid #138781;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    


    
}
.title-heading
{
    text-align: center;
    margin-top: 10px;
    color: aqua;
}



/* ======MOBILE VIEW===== */

/* Mobile specific styles */

@media (max-width: 600px) {
    /* Project Section (Mobile) */
    .project {
        padding: 10px;
        margin-left: 10px;       
        display: flex;
        flex-direction: column;  
        justify-content: flex-start; 
        text-align: left;        
        margin-bottom: 40px;     
        width: 90%;              
    }

    /* Adjustments for the project box in mobile view */
    
        .project-box {
            margin-left: 10px; 
            margin-top: 20px; 
            margin-bottom: 30px;
            padding: 10px; 
            font-size: 18px; 
            text-align: center; 
            background-color: #DAA520; 
            border: 2px solid #d3d3d3; 
            border-radius: 8px;
            display: flex;
            justify-content: space-between; /* Title on the left, icon on the right */
            align-items: center;
            cursor: pointer;
          }
    

    /* Ensures the toggle icon is aligned to the right */
    .toggle-icon {
        margin-left: auto;
        font-size: 24px;
    }

    /* Row container (Mobile) */
    .row {
        display: flex;
        flex-direction: column; 
        align-items: center; 
        width: 100%; 
        margin-top: 30px;
        padding: 0;
    }

    /* Column inside the row (Mobile) */
    .col-md-4.second {
        width: 90%; 
        margin: 15px 0; 
        display: flex;
        flex-direction: column;
        align-items: center; 
        justify-content: center; 
    }

    /* Card styling (Mobile) */
    .card {
        width: 100%; 
        padding: 15px;
        text-align: center;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
        border-radius: 10px;
    }

    /* Card image (Mobile) */
    .card-image img {
        width: 100%; 
        height: auto; 
        border-radius: 10px;
    }

    /* Card image overlay styling (Mobile) */
    .card-image-overlay {
        display: flex;
        justify-content: center; 
        margin-top: 10px;
        flex-wrap: wrap;
    }

    /* Card badge styling (Mobile) */
    .card-detail-badge {
        background-color: #007bff;
        color: white;
        padding: 5px 10px;
        border-radius: 5px;
        margin: 5px; 
    }

    /* Centers the ads container */
    #ads {
        margin: auto;
    }
}


