
/* style for education details */

.education
{
    margin: 80px 0px;
    margin-left: 140px;
    
    background-color: white;
    align-items: center;
   

}

 .education h1
{
    color: black;
    text-align: left;
    font-family: 'Oswald', sans-serif;
    margin-bottom: 50px;
    font-size: 35px;
}


/* ======MOBILE VIEW====== */

@media (max-width : 600px)
{
    .education
    {
        margin: 0;
        padding: 0;
        margin-top: 20px;
    }
    .education hr
{
    color: green;
    width: 200px;
    margin:10px auto;
}
}