
/* style for the expirince */

.Experience
{
    margin: 50px 0px;
    margin-left: 140px;
    
    background-color: white;
    align-items: center;
    
    
}

 .Experience h1
{
    
    color: black;
    text-align: left;
    font-family: 'Oswald', sans-serif;
    margin-bottom: 50px;
    font-size: 35px;
}





/* ==========Mobile view========= */

@media (max-width: 600px)
{
    .Experience
    {
        margin: 0;
        padding: 0;

    }
    .Experience hr
{
    color: green;
    width: 200px;
    margin:10px auto;
}
}