
/* stylig the technologies */

.container3
{
  margin-top: 150px;
  text-align: left;
  margin-left: 120px;
  font-family: 'Oswald', sans-serif;
}

.row3 {
    display: flex; 
    flex-wrap: wrap;
    justify-content: left; 
    
    padding: 20px;
    margin: 20px auto;
   
    border-radius: 20px;
    width: 1100px;
    height: auto;
    
}

.card-body {
    width: auto;
    height: auto;
    padding: 10px;
    justify-content: left;
    
}

.main1
{
 box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; 
}
.main1 :hover
{
    background: rgb(6, 118, 179);
    color: white;
    transition: 1s;
}


.media-body {
    display: flex;
    flex-direction: column;
    justify-content: left;
    width: auto;
    
}
.media {
    display: flex; 
    align-items: center; 
    gap: 10px;
} 

.tech-icon {
    font-size: 20px; 
}



  /* ======MOBILE VIEW===== */

  @media (max-width: 600px) {
    .container3 {
      text-align: left;
      justify-content: center ; 
    }
  
    .row3 {
      display: flex;
      flex-direction: column; 
      align-items: center; 
      width: 100%; 
      margin: 0;
      padding: 0;
    }
  
    .col-md-3 {
      width: 90%; 
      margin: 15px 0; 
      display: flex;
      justify-content: center; 
    }
  
    .card {
      width: 100%; 
      padding: 10px;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease, width 0.3s ease; 
    }
  
    /* Hover effect for the card */
    
    .card:hover {
      transform: translateY(-10px); 
      box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.3); 
      background: rgb(6, 118, 179); 
      width: 100%;
    }
  
    .tech-icon {
      font-size: 50px; 
      margin-bottom: 10px;
    }
  
    h5 {
      text-align: center;
    }
  }
  
  
  
