/* total body colour */
body {
    background-color: #fff;
}

/* style for side bar */
.sidebar-section {
    display: flex;
    background-color: white; 
    margin-top: 0;
}

.sidebar {
    height: 100vh;
    background-color: #dfe6e9;   
    width: 90px;
    position: fixed;
    top: 0; 
    left: 0;
}

/* sidebar toggle function styling */
.sidebar-toggle {
    width: 200px;
}

.sidebar-toggle-icons {
    display: flex;
    align-items: right;
    justify-content: right;
    cursor: pointer;
}

.sidebar-toggle-icons p svg {
    background-color:  #dfe6e9;   
    
    color: #2c2c2c;
    margin-top: 10px;
}




/* ==============mobile view code=================== */

/* ====MOBILE VIEW=== */
@media (max-width: 600px) {
    .sidebar {
        display: none;
    }
}
